import React, { useEffect } from "react"
import { AnimatedCursor } from "../../../components/utils/AnimatedCursor"
import AOS from "aos"
import "aos/dist/aos.css"
import Layout from "../../../components/Layout"
import CareerDetail from "../../../components/CareerDetail"
import positions from '../../../components/shared/Data/positions'
export default function SinglePosition(req, res) {
  const {id} = req.params
  React.useEffect(() => {
    if (document !== "undefined" && window !== "undefined") {
      const script = document.createElement("script")
      script.type = "text/javascript"
      script.src = "https://widget.clutch.co/static/js/widget.js"
      script.async = true
      document.body.appendChild(script)
      // run script
      script.onload = () => {
        window.CLUTCHCO.Init()
      }
      return () => {
        window.CLUTCHCO.Destroy()
        document.body.removeChild(script)
      }
    }
  }, [])
  useEffect(() => {
    AOS.init()
  }, [])
  const mouseCoords =
    typeof window !== "undefined" &&
    JSON.parse(window.localStorage.getItem("mouseCoords"))

    const positionDetails = positions.filter(pos => pos.id == id)
    console.log({positionDetails})

  return (
    <Layout>
      <CareerDetail details={positionDetails[0]} />
      <AnimatedCursor
        coord={mouseCoords}
        innerSize={16}
        outerSize={16}
        color="255, 255, 255"
        outerAlpha={0.2}
        innerScale={0.8}
        outerScale={4}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
          ".swiper-pagination-bullet",
        ]}
      />
    </Layout>
  )
}
