import React, { useEffect, useState } from "react"
import Button from "../../shared/Button"
import { styled } from "@mui/material/styles"
import { TextField, Grid, Alert } from "@mui/material"
import "./Footer.scss"
import Select from "./SelectHook"
import { navigate } from "gatsby"
import Loader from "../../utils/Loader"

import UploadAttachment from "./UploadAttachment"
import { fileToAttachmentFormat, sendMail } from "../../utils/helpers"
// import useFetch from "../../utils/hubspotRequest"
const CssTextField = styled(TextField)({
  "& .MuiFormLabel-root": {
    color: "#2F2F2F",
    fontFamily: "Poppins",
    fontWeight: 500,
    fontSize: "1rem",
    marginBottom: "6px",
    lineHeight: "1rem",
  },
  "& .MuiInputBase-root": {
    fontFamily: "'PoppinSemi'",
    fontSize: "1.25rem",
    lineHeight: "115.5%",
    color: "#2F2F2F",
  },
  "& .MuiFormLabel-asterisk.MuiInputLabel-asterisk": {
    color: "red",
  },
  "& label.Mui:after": {
    color: "#B5B5B5",
  },
  "& label.Mui-focused": {
    color: "#B5B5B5",
  },

  "& .MuiInput-underline:after": {
    borderBottom: "2px solid #183266",
  },
  "& .MuiInput-root:before": {
    borderBottom: 0,
    borderBottom: "2px solid #E6E6E6",
  },
  "& .MuiInput-input": {
    cursor: "none",
  },
  "& .MuiInput-input": {
    cursor: "none",
  },
})

const helpOptions = [
  {
    id: 1,
    name: "SQA Engineer",
    value: "SQA Engineer",
    budget: false,
  },
  {
    id: 2,
    name: "Software Engineer (Python)",
    value: "Software Engineer (Python)",
    budget: false,
  },
  {
    id: 3,
    name: "Software Engineer (ReactJS)",
    value: "Software Engineer (ReactJS)",
    budget: false,
  },
]
const budgetOptions = [
  {
    id: 1,
    name: "$5000-$10000",
    value: "5000-10000",
  },
  {
    id: 2,
    name: "$10000-$50000",
    value: "10000-50000",
  },
  {
    id: 3,
    name: "$50000+",
    value: "50000",
  },
]

export default function ContactForm({ position }) {
  const [open, setOpen] = React.useState(false)
  const [file, setFile] = React.useState(null)
  const [cv, setCV] = React.useState(null)
  const [isFailed, setIsFailed] = React.useState(false)
  const [clearSelectFields, setClearSelectFields] = React.useState(false) //1

  const [state, setState] = useState({
    name: "",
    email: "",
    linkedin: "",
    feild: "",
    message: "",
  })
  useEffect(() => {
    setState({
      ...state,
      feild: position,
    })
  }, [position])

  const [budgetDisable, setbudgetDisable] = useState(true)

  const onChangeHandler = e => {
    const value = e.target.value
    if (e.target.name === "feild") {
      const value = helpOptions.filter(item => item.name === e.target.value)
      setbudgetDisable(value[0].budget)
    }
    setState({
      ...state,
      [e.target.name]: value,
    })
    setIsFailed(false)
  }

  async function handleSubmit(e) {
    e.preventDefault()
    if (state.name.length > 25) return
    setOpen(true)
    let form = document.forms["contact"]
    // fetch(
    //   "https://script.google.com/macros/s/AKfycbyVDyCG6u76JONl627-Z6vDPNXB8eFSLjlabDx4gUI4Qf_lSGZL40869BC2r50BiX3BVg/exec",
    //   { method: "POST", body: new FormData(form) }
    // )
    //   .then(() => {
    //     navigate("/thank-you")
    //   })
    //   .catch(error => console.error("Error!", error.message))

    let cvToSend = await fileToAttachmentFormat(cv, state.name, "cv")
    let otherfile = await fileToAttachmentFormat(
      file,
      state.name,
      "additional-file"
    )

    const body = {
      to: state.email, // Change to your recipient
      //to: "careers@codefulcrum.com", // Change to your recipient
      subject: `Job Application for ${position}`,
      name: `Job Application: ${state.name}`,
      html: `
      <div>
      <div><b>Hi,</b> </div>
      <div>${state.message} </div>
      <br />
      <div><b>Name:</b> ${state.name} </div>
      <div><b>Email:</b> ${state.email} </div>
      <div><b>Linkedin:</b> ${state.linkedin} </div>
      </div>
      `,
      attachments: [cvToSend, otherfile].filter(file => file !== null),
    }

    sendMail(body)
      .then(res => {
        if (res.status == 200) {
          navigate("/thank-you")
        } else {
          setIsFailed(true)
        }
        setOpen(false)
      })
      .catch(err => {
        console.error({ err })
        setIsFailed(true)
      })
  }

  const errorText = <span style={{ color: "red" }}>Enter a valid name</span>

  const clearButton = () => {
    const form = document.forms["contact"]
    form.reset()
    setClearSelectFields(true)
  }

  return (
    <>
      {isFailed && (
        <Alert variant="outlined" severity="error" className="mui-alert">
          Message sending failed
        </Alert>
      )}
      <Loader open={open} />
      <form name="contact" onSubmit={handleSubmit}>
        <Grid container columnSpacing={5} rowSpacing={6}>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Name"
              variant="standard"
              name="name"
              fullWidth
              required
              helperText={state.name.length > 25 && errorText}
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Email"
              variant="standard"
              name="email"
              fullWidth
              required
              type="email"
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Linkedin"
              variant="standard"
              name="linkedin"
              fullWidth
            />
          </Grid>

          <Grid item md={6} sm={12} xs={12} className="form-fields">
            <Select
              options={helpOptions}
              label="Field"
              name="feild"
              value={state.feild}
              disabled
              onChange={onChangeHandler}
              clearSelectFields={clearSelectFields}
              resetSelectClearState={() => setClearSelectFields(false)}
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12} className="form-fields">
            <UploadAttachment
              label="Upload CV"
              value={cv}
              required
              onChange={file => setCV(file)}
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12} className="form-fields">
            <UploadAttachment
              label="Additional File"
              value={file}
              onChange={file => setFile(file)}
            />
          </Grid>
          <Grid item md={12} xs={12} className="form-fields">
            <textarea
              onChange={onChangeHandler}
              name="message"
              id=""
              cols="30"
              rows="4"
              placeholder="Why would you like to join CodeFulcrum?"
            ></textarea>
          </Grid>
        </Grid>
        <Grid className="text-right  pe-0 pt-5">
          <Button
            text="CANCEL"
            variant="fill"
            border="#2f2f2f"
            bgClr="#ffffff"
            clr="#2f2f2f"
            className="me-3"
            onClick={clearButton} //3
            type="button"
          />
          <Button
            text="SEND MESSAGE"
            variant="fill"
            bgClr="#f23b48"
            type="submit"
          />
        </Grid>
      </form>
    </>
  )
}
