import React, { useState } from "react"
import styled from "styled-components"

export const UploadIcon = () => {
  return (
    <svg
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M12 5L12.3536 4.64645L12 4.29289L11.6464 4.64645L12 5ZM11.5 17C11.5 17.2761 11.7239 17.5 12 17.5C12.2761 17.5 12.5 17.2761 12.5 17L11.5 17ZM18.3536 10.6464L12.3536 4.64645L11.6464 5.35355L17.6464 11.3536L18.3536 10.6464ZM11.6464 4.64645L5.64645 10.6464L6.35355 11.3536L12.3536 5.35355L11.6464 4.64645ZM11.5 5L11.5 17L12.5 17L12.5 5L11.5 5Z"
        fill="#333333"
      />
      <path d="M5 21H19" stroke="#333333" />
    </svg>
  )
}

const StyledUploadField = styled.label`
  width: 100%;
  padding: 8px 0;
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  align-items: baseline;
  .left-side {
    label {
      font-family: "Poppins";
      font-style: normal;
      font-weight: 500;
      font-size: 1rem;
      line-height: 115.5%;
      color: #2f2f2f;
      :after {
        content: "*";
        color: red;
        display: ${props => !props.required && "none"};
      }
    }
    flex: 1;
    .uploaded-file {
      height: 25px;
    }
  }
`
const StyledInput = styled.input`
  display: none;
`

export default function UploadAttachment({
  label,
  onChange,
  value,
  Icon,
  required,
}) {
  const id = `upload-file-${Math.floor(Math.random() * 10)}`
  console.log({ required })
  return (
    <>
      <StyledUploadField htmlFor={id} required={required}>
        <div className="left-side">
          <label> {label} </label>
          <div className="uploaded-file"> {value?.name} </div>
        </div>
        <div className="right-side">
          <Icon />
        </div>
      </StyledUploadField>

      <StyledInput
        type="file"
        accept=".pdf"
        id={id}
        onChange={e => onChange(e.target.files[0])}
      />
    </>
  )
}

UploadAttachment.defaultProps = {
  label: "Upload Attachment",
  onChange: file => {},
  value: null,
  Icon: UploadIcon,
  required: false,
}
